import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `StaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `StaticQuery`: https://gatsby.dev/staticquery
 */

const Image = (props) => (
  <StaticQuery
    query={graphql`
      query {

        p1: file(relativePath: { eq: "portfolio/1.png" }) {
          childImageSharp {
            fluid(maxWidth: 710, maxHeight: 585, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        img20181: file(relativePath: { eq: "studio/year18/1.png" }) {
          childImageSharp {
            fluid(maxWidth: 610, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }


        img20182: file(relativePath: { eq: "studio/year18/2.png" }) {
          childImageSharp {
            fluid(maxWidth: 580, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        img20183: file(relativePath: { eq: "studio/year18/3.png" }) {
          childImageSharp {
            fluid(maxWidth: 594, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        img20184: file(relativePath: { eq: "studio/year18/4.png" }) {
          childImageSharp {
            fluid(maxWidth: 424, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        img20185: file(relativePath: { eq: "studio/year18/5.png" }) {
          childImageSharp {
            fluid(maxWidth: 441, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        img20186: file(relativePath: { eq: "studio/year18/6.png" }) {
          childImageSharp {
            fluid(maxWidth: 590, maxHeight: 420, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        pimg1: file(relativePath: { eq: "works/pimg1.png" }) {
          childImageSharp {
            fluid(maxWidth: 550, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pimg2: file(relativePath: { eq: "works/pimg2.png" }) {
          childImageSharp {
            fluid(maxWidth: 290, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        pimg3: file(relativePath: { eq: "works/pimg3.png" }) {
          childImageSharp {
            fluid(maxWidth: 620, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        aw1: file(relativePath: { eq: "works/aw1.png" }) {
          childImageSharp {
            fluid(maxWidth: 441, maxHeight: 516,  quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        aw2: file(relativePath: { eq: "works/aw2.png" }) {
          childImageSharp {
            fluid(maxWidth: 341, maxHeight: 446, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

      background: file(relativePath: { eq: "studio/Homepage-background.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920, maxHeight: 1080, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        arsen1: file(relativePath: { eq: "Arsen-1.png" }) {
          childImageSharp {
            fluid(maxWidth: 400, maxHeight: 560, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        st1: file(relativePath: { eq: "studio/1.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }

        st2: file(relativePath: { eq: "studio/2.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        st3: file(relativePath: { eq: "studio/3.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        st4: file(relativePath: { eq: "studio/4.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        
        st5: file(relativePath: { eq: "studio/5.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        
        st6: file(relativePath: { eq: "studio/6.png" }) {
          childImageSharp {
            fluid(maxWidth: 300, maxHeight: 300, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => <Img fluid={data[props.image].childImageSharp.fluid} />}
  />
)
export default Image
